import { PageScaffold } from "@/components/page-scaffold";

export default function PatientsListPage() {
  return (
    <PageScaffold
      code="PRO-02"
      title="Lista de Pacientes"
      objective="Tabela CRM com status, risco, protocolo ativo e próximo check-in."
      primaryAction={{ label: "Adicionar paciente", href: "/patients/new" }}
      sections={[
        "Colunas: nome, status, risk, protocolo, próximo check-in",
        "Filtros: status, tags e busca",
        "Ações: perfil, mensagem, arquivar",
      ]}
    />
  );
}
